


.Toolbar {
  height: 45px;
  border: 1px solid lightgrey;
  border-width: 0.5px 0px 0.5px 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 10px 0 10px;
}

.Toolbar-view-buttons button
 {
  cursor: pointer;
  font-size: 1rem;
  height: 35px;
  background-color: white;
  border: 1px solid #DFDFDF;
  border-radius: 8px 0 0 8px;
  padding: 0 10px 0 10px;
}

.Toolbar-view-buttons .selected {
  color: #666666;
  background-color: #e5f1fb;
}

.Toolbar-view-buttons button:first-child {
  border-radius: 8px 0 0 8px;
}

.Toolbar-view-buttons button:last-child {
  border-left: 0px;
  border-radius: 0 8px 8px 0;
}

.Toolbar-view-button button.btn-group button:not(:last-child) {
    border-right: none; /* Prevent double borders */
}

.Toolbar-view-buttons button:hover{
	background:lightgray;
}

.Toolbar-view-buttons, .Toolbar-view-dropdowns {
	display: inherit;
	z-index: 2;
}

.Toolbar-dropdown {
  height: 35px;
  font-size: 1rem;
  border: 1px solid #DFDFDF;
  border-radius: 8px;
  margin: 3px;
	background: white;
	cursor: pointer;
}

.Toolbar-dropdown:disabled {
	cursor: initial;
}

.Toolbar-searchMode label {
	vertical-align:middle;
}
.Toolbar-searchMode input {
	display:inline-block;
	vertical-align:middle;
	height:35px;
	z-index: 5;
	cursor: pointer;
}

.Toolbar-searchMode {
  font-size: 1rem;
	position: absolute;
	left: 0;
	right: 0;
}

.Toolbar-searchMode input:disabled {
	color: lightgray;
	cursor: not-allowed;
}




